import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import assemblyImage from '../../assets/DIY-01.png'; // 
import './Game2.scss'; // 

const Game2 = () => {
  const [isAssembling, setIsAssembling] = useState(false); // 
  const [showFinishButton, setShowFinishButton] = useState(false); // 
  const [showEndButton, setShowEndButton] = useState(false); // 
  const navigate = useNavigate();

  // 
  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 11 } });
  };

  // 
  const handleStartAssembling = () => {
    setIsAssembling(true); // 
    setTimeout(() => {
      setShowFinishButton(true); // 1.5 
    }, 900); // 90000  = 1.5 
  };

  // 
  const handleFinishAssembly = () => {
    setIsAssembling(false); // 
    setShowFinishButton(false); // 
    setShowEndButton(true); // 
  };

  return (
    <div className="container2">
      <div className="content2">
        {/*  */}
        {!isAssembling && !showEndButton && (
          <button className="assembleButton2" onClick={handleStartAssembling}>
            
          </button>
        )}
        {/*  */}
        {isAssembling && (
          <>
            <img src={assemblyImage} alt="" className="assemblyImage2" />
            {showFinishButton && (
              <button className="finishButton2" onClick={handleFinishAssembly}>
                
              </button>
            )}
          </>
        )}
        {/*  */}
        {showEndButton && (
          <button className="endButton2" onClick={handleEndGame}>
            !
          </button>
        )}
      </div>
    </div>
  );
};

export default Game2;
