import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { RetweetOutlined, AudioOutlined, EyeOutlined } from '@ant-design/icons'; //  Ant Design 
import mapImageFront from '../../assets/.jpg'; // 
import mapImageBack from '../../assets/.jpg'; // 
import audio1 from '../../assets/3-1.mp3'; // 1
import audio2 from '../../assets/3-2.mp3'; // 2
import audio3 from '../../assets/3-3.mp3'; // 3
import './Game4.scss'; //  SCSS 

const Game4 = () => {
  const [inputValue, setInputValue] = useState(''); // 
  const [showEndButton, setShowEndButton] = useState(false); // 
  const [isFront, setIsFront] = useState(true); // 
  const [inputVisible, setInputVisible] = useState(true); // 
  const [showNewPrompt, setShowNewPrompt] = useState(false); // 
  const [showNewButton, setShowNewButton] = useState(false); // 
  const [audioPlaying, setAudioPlaying] = useState(false); // 
  const navigate = useNavigate(); // 

  // 
  const handlePlayAudio = (index) => {
    if (audioPlaying) return; // 

    setAudioPlaying(true);
    const audioFiles = [audio1, audio2, audio3]; // 
    const audio = new Audio(audioFiles[index]);
    audio.play();

    // 
    audio.onended = () => {
      setAudioPlaying(false);
    };

    // 
    if (index === 1) {
      checkInputValue();
    }
  };

  // 
  const checkInputValue = () => {
    if (inputValue === '') {
      setShowEndButton(true); // 
    }
  };

  // 
  const handleEndGame = () => {
    setShowNewPrompt(true); // 
    setTimeout(() => {
      setShowNewButton(true); // 3
    }, 3000);
  };

  // 
  const handleNavigate = () => {
    navigate('/', { state: { dialogIndex: 37 } });
  };

  // 
  const clearResultAndAudio = () => {
    setInputValue(''); // 
    setShowEndButton(false); // 
    setShowNewPrompt(false); // 
    setShowNewButton(false); // 
  };

  // 
  const toggleMap = () => {
    setIsFront(!isFront); // 
  };

  // 
  const toggleInputVisibility = () => {
    setInputVisible(!inputVisible); // 
  };

  return (
    <div className="container4">
      <div className="mapContainer">
        {/*  */}
        <div className="audioButtonsContainer">
          {[audio1, audio2, audio3].map((audio, index) => (
            <button
              key={index}
              className="audioButton"
              onClick={() => handlePlayAudio(index)}
              disabled={audioPlaying}
            >
              <AudioOutlined />
            </button>
          ))}
        </div>

        {/*  */}
        <div className="mapImageContainer">
          <img src={isFront ? mapImageFront : mapImageBack} alt="" className="mapImage" />

          {/* / */}
          <div className="mapButtonsContainer">
            <button className="mapToggleButton" onClick={toggleMap}>
              <RetweetOutlined />
            </button>
            <button className="inputToggleButton" onClick={toggleInputVisibility}>
              <EyeOutlined />
            </button>
          </div>

          {/*  */}
          {inputVisible && (
            <div className="resultContainer">
              <input
                type="text"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                onBlur={checkInputValue}
                placeholder=""
                className="input"
              />
              {showEndButton && (
                <button className="endButton" onClick={handleEndGame}>
                  
                </button>
              )}
            </div>
          )}
        </div>
      </div>

      {/*  */}
      <div className={`centralContainer ${showNewPrompt || showNewButton ? 'visible' : ''}`}>
        {showNewPrompt && (
          <div className="newPrompt">
            
          </div>
        )}
        {showNewButton && (
          <button className="navigateButton" onClick={handleNavigate}>
            
          </button>
        )}
      </div>
    </div>
  );
};

export default Game4;
