import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { AudioOutlined, AudioMutedOutlined } from '@ant-design/icons';
import alertSound from '../../assets/alert.mp3'; // 
import './Game3.scss'; //  SCSS 

const Game3 = () => {
  const [step, setStep] = useState(1); // 
  const [inputValue, setInputValue] = useState(''); // 
  const [isCorrect, setIsCorrect] = useState(false); // 
  const [showFindFragmentButton, setShowFindFragmentButton] = useState(false); // 
  const [showPuzzleButton, setShowPuzzleButton] = useState(false); // 
  const [showGuideMessage, setShowGuideMessage] = useState(false); // 
  const [showFinalButton, setShowFinalButton] = useState(false); // 
  const [isAlertPlaying, setIsAlertPlaying] = useState(false); // 

  const navigate = useNavigate(); //  Hook
  const audioRef = useRef(null); // 

  // 
  const handleInputChange = (e) => {
    const value = e.target.value;
    setInputValue(value); // 
    setIsCorrect(value === ''); // 
  };

  // 
  const handleEndGame = () => {
    if (audioRef.current) {
      audioRef.current.pause(); // 
      audioRef.current.currentTime = 0; // 
    }
    setShowGuideMessage(true); // 
    setStep(4); // 4
    setTimeout(() => {
      setShowFinalButton(true); // 3.6
    }, 3600); // 3600  = 3.6 
  };

  // 
  const handleFinalButtonClick = () => {
    navigate('/', { state: { dialogIndex: 22 } }); // 
  };

  // 
  const handleButtonClick = () => {
    setStep(step + 1); // 
    setShowFindFragmentButton(false); // 
    setShowPuzzleButton(false); // 
  };

  // 
  useEffect(() => {
    audioRef.current = new Audio(alertSound); // 
    audioRef.current.loop = true; // 

    // 
    return () => {
      if (audioRef.current) {
        audioRef.current.pause(); // 
        audioRef.current.currentTime = 0; // 
      }
    };
  }, []);

  // 
  useEffect(() => {
    if (step === 1) {
      setTimeout(() => {
        setShowFindFragmentButton(true); // 9
      }, 9000); // 9000  = 9 
    } else if (step === 2) {
      setTimeout(() => {
        setShowPuzzleButton(true); // 3
      }, 3000); // 3000  = 3 
    }
  }, [step]);

  // 
  const toggleAlert = () => {
    if (isAlertPlaying) {
      audioRef.current.pause();
    } else {
      audioRef.current.play().catch((error) => {
        console.error('Audio playback was prevented:', error);
      });
    }
    setIsAlertPlaying(!isAlertPlaying);
  };

  return (
    <div className="container3">
      <div className="alert-toggle" onClick={toggleAlert}>
        {isAlertPlaying ? <AudioOutlined /> : <AudioMutedOutlined />}
      </div>
      {step === 1 && (
        <div className="content3">
          <p className="text3">! </p>
          {showFindFragmentButton && (
            <button className="button3" onClick={handleButtonClick}></button>
          )}
        </div>
      )}
      {step === 2 && (
        <div className="content3">
          <p className="text3">!</p>
          {showPuzzleButton && (
            <button className="button3" onClick={handleButtonClick}></button>
          )}
        </div>
      )}
      {step === 3 && (
        <div className="content3">
          <input
            type="text"
            placeholder=""
            value={inputValue}
            onChange={handleInputChange}
            className="input3"
          />
          {isCorrect && (
            <button className="button3 endButton3" onClick={handleEndGame}></button>
          )}
        </div>
      )}
      {step === 4 && showGuideMessage && (
        <div className="content3">
          <p className="text3">!</p>
          {showFinalButton && (
            <button className="button3 finalButton3" onClick={handleFinalButtonClick}></button>
          )}
        </div>
      )}
    </div>
  );
};

export default Game3;
